input[type="range"] {
    /* Change the appearance of the sliders */
    -webkit-appearance: none;
    height: 15px;
    background-color: #5361ab;
    outline: none;
    border-radius: 20px;
    margin-left:7px;
    margin-right:7px;
    padding-left: 2px;
    padding-right: 2px;
  }

    input::-webkit-slider-thumb {
      
      -webkit-appearance: none;
      width: 12px;
      height: 12px;
      background-color: #131533;
      border-radius: 50%;
      cursor: pointer
      ;
    }
    input::-webkit-slider-thumb:hover{
      background-color:#5361ab;
      transform: scale(1.5);
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
      
      
    }
    input::-moz-range-thumb {
      width: 12px;
      height: 12px;
      background-color: #131533;
      border-radius: 50%;
      cursor: pointer;
    }
